export default "import {\n  CornerstoneImage,\n  CornerstoneViewer,\n  ImageStoreProvider,\n  InsightViewerContainer,\n  InsightViewerControllerOptions,\n  InsightViewerTestController,\n  installWADOImageLoader,\n  ProgressViewer,\n  useImageStore,\n} from '@lunit/insight-viewer';\nimport { Button } from '@material-ui/core';\nimport React, { useEffect, useMemo, useState } from 'react';\nimport series from '../../../__fixtures__/series.json';\n\ninstallWADOImageLoader();\n\nconst controllerOptions: InsightViewerControllerOptions = {\n  width: [600, 400, 1000],\n  height: [700, 400, 1000],\n  control: ['pan', ['none', 'pan', 'adjust']],\n  wheel: ['zoom', ['none', 'zoom']],\n  flip: [false],\n  invert: [false],\n};\n\nconst imageIds: string[] = series\n  .slice(0, 10)\n  .map((p) => `wadouri:https://fixtures.front.lunit.io/dcm-files/series/${p}`);\n\nfunction Sample() {\n  const { fetch } = useImageStore();\n\n  const [imageId, setImageId] = useState<string>(() => imageIds[0]);\n\n  const image = useMemo<CornerstoneImage>(() => fetch(imageId), [fetch, imageId]);\n\n  useEffect(() => {\n    // 1 ~ 4번 이미지들을 Prefetch 한다\n    // 1 ~ 4번 이미지들은 빠르게 뜨게 될 것이다\n    fetch(imageIds[1]);\n    fetch(imageIds[2]);\n    fetch(imageIds[3]);\n    fetch(imageIds[4]);\n  }, [fetch]);\n\n  return (\n    <div style={{ display: 'flex' }}>\n      <InsightViewerTestController options={controllerOptions}>\n        {({ width, height, invert, flip, control, wheel, resetTime, element, setElement, interactions }) => (\n          <InsightViewerContainer width={width} height={height}>\n            <CornerstoneViewer\n              width={width}\n              height={height}\n              invert={invert}\n              flip={flip}\n              interactions={interactions}\n              resetTime={resetTime}\n              image={image}\n              updateCornerstoneRenderData={() => {}}\n            />\n            <ProgressViewer width={width} height={height} image={image} />\n          </InsightViewerContainer>\n        )}\n      </InsightViewerTestController>\n      <div>\n        <ul>\n          {imageIds.map((imageId, i) => (\n            <li key={imageId}>\n              <Button onClick={() => setImageId(imageId)}>Show Image {i}</Button>\n            </li>\n          ))}\n        </ul>\n      </div>\n    </div>\n  );\n}\n\n// 상위에 <ImageStoreProvider>가 반드시 필요하다\nexport default () => (\n  <ImageStoreProvider>\n    <Sample />\n  </ImageStoreProvider>\n);\n"